/* Responsive styles for catalog-only application */
@media (max-width: 768px) {
  .flex-col-mobile {
    flex-direction: column;
  }
  
  .w-full-mobile {
    width: 100%;
  }
  
  .mt-4-mobile {
    margin-top: 1rem;
  }
  
  .gap-2-mobile {
    gap: 0.5rem;
  }
  
  .text-sm-mobile {
    font-size: 0.875rem;
  }
  
  .px-2-mobile {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  
  .py-4-mobile {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
  
  .hidden-mobile {
    display: none;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .px-4-tablet {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .py-2-tablet {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
  }
}

@media (min-width: 1025px) {
  .max-w-7xl {
    max-width: 80rem;
  }
}
