/* Simplified styles for catalog-only application */
.dark-mode {
  --bg-primary: #1a202c;
  --text-primary: #f7fafc;
  --bg-secondary: #2d3748;
  --text-secondary: #e2e8f0;
  --border-color: #4a5568;
  --accent-color: #4299e1;
}

.dark-mode body {
  background-color: var(--bg-primary);
  color: var(--text-primary);
}

.dark-mode .bg-white {
  background-color: var(--bg-secondary);
}

.dark-mode .bg-gray-50 {
  background-color: var(--bg-primary);
}

.dark-mode .bg-gray-100 {
  background-color: #2d3748;
}

.dark-mode .text-gray-500,
.dark-mode .text-gray-600,
.dark-mode .text-gray-700,
.dark-mode .text-gray-800,
.dark-mode .text-gray-900 {
  color: var(--text-secondary);
}

.dark-mode .border-gray-300 {
  border-color: var(--border-color);
}

.dark-mode .bg-gray-200 {
  background-color: #4a5568;
}

.dark-mode .bg-primary-600 {
  background-color: #3182ce;
}

.dark-mode .bg-primary-700 {
  background-color: #2b6cb0;
}

.dark-mode .text-primary-600 {
  color: #63b3ed;
}

.dark-mode .shadow-md,
.dark-mode .shadow-sm {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.2);
}

.dark-mode input,
.dark-mode select,
.dark-mode textarea {
  background-color: #2d3748;
  color: #e2e8f0;
  border-color: #4a5568;
}

.dark-mode .bg-green-100 {
  background-color: #1c4532;
}

.dark-mode .bg-yellow-100 {
  background-color: #744210;
}

.dark-mode .bg-blue-100 {
  background-color: #2a4365;
}

.dark-mode .text-green-800 {
  color: #9ae6b4;
}

.dark-mode .text-yellow-800 {
  color: #fbd38d;
}

.dark-mode .text-blue-800 {
  color: #90cdf4;
}

/* Responsive table for mobile */
@media (max-width: 768px) {
  table {
    display: block;
    overflow-x: auto;
  }
  
  .line-clamp-2 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
